@font-face {
  font-family: 'samim';
  src: url('./fonts/samim/Samim-FD-WOL.eot'), /* IE9 Compat Modes */
    url('./fonts/samim/Samim-FD-WOL.woff2') format('woff2'), /* Super Modern Browsers */
    url('./fonts/samim/Samim-FD-WOL.woff') format('woff'), /* Pretty Modern Browsers */
    url('./fonts/samim/Samim-FD-WOL.ttf')  format('truetype'); /* Safari, Android, iOS */
}

@font-face {
  font-family: 'samim-medium';
  src: url('./fonts/samim/Samim-Medium-FD-WOL.eot'), /* IE9 Compat Modes */
    url('./fonts/samim/Samim-Medium-FD-WOL.woff2') format('woff2'), /* Super Modern Browsers */
    url('./fonts/samim/Samim-Medium-FD-WOL.woff') format('woff'), /* Pretty Modern Browsers */
    url('./fonts/samim/Samim-Medium-FD-WOL.ttf')  format('truetype'); /* Safari, Android, iOS */
}

@font-face {
  font-family: 'samim-bold';
  src: url('./fonts/samim/Samim-Bold-FD-WOL.eot'), /* IE9 Compat Modes */
    url('./fonts/samim/Samim-Bold-FD-WOL.woff2') format('woff2'), /* Super Modern Browsers */
    url('./fonts/samim/Samim-Bold-FD-WOL.woff') format('woff'), /* Pretty Modern Browsers */
    url('./fonts/samim/Samim-Bold-FD-WOL.ttf')  format('truetype'); /* Safari, Android, iOS */
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: samim, sans-serif;
  direction: rtl;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mr-auto {
  margin-right: auto;
}

.bg-white {
  background-color: #fff;
}

.bg-first {
  background-color: #E4F2F7;
}

.bg-second {
  background-color: #EBC071;
}

.btn-first {
  background-color: #EBC071;
  color: #333;
  border-radius: 5px;
  border: none;
}

.btn-second {
  background-color: #D9D9D9;
  color: #333;
  border-radius: 5px;
  border: none;
}

.btn-first:hover {
  background-color: #c08f35;
  color: #fff;
}

.btn-second:hover {
  color: #333;
}

.text-xs {
  font-size: .7rem;
}

.text-s {
  font-size: .9rem;
}

.text-lg {
  font-size: 1.2rem;
}

.text-xl {
  font-size: 1.5rem;
}

.text-x2l {
  font-size: 2rem;
}

.link {
  text-decoration: none;
  color: #333;
}

.link:hover {
  text-decoration: none;
  cursor: pointer;
}

.link-primary:hover {
  color: #E4F2F7;
}

.link-secondary:hover {
  color: rgb(0, 0, 255);
}

.select {
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: .5rem;
}

.bordered {
  border: 1px solid #ccc;
  border-radius: 5px;
}

i.fa-trash:hover {
  cursor: pointer;
}

.count {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  background-color: #D9D9D9;
  border-radius: 50%;
  border: none;
  padding: 2px;
}

.count-active {
  background-color: #000;
  color: #fff;
}

.table thead th {
  border-bottom: 1px solid #ccc;
  border-top: none;
  font-weight: 500;
}

textarea {
  resize: none;
}

.dark-overlay {
  background-color: #000;
  opacity: 60%;
  position: relative;
  top: 70px;
  left: 0;
  width: 100%;
  height: 300px;
}